* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: "Open Sans", sans-serif;
}

.Home {
  height: 100vh;
  position: relative;
}
.Home header {
  display: flex;
  background-color: #141A46;
  position: relative;
  top: 0;
  left: 0;
  font-size: 2.5rem;
  justify-content: space-between;
  align-items: center;
}
.Home .top-bar ul {
  display: flex;
  align-items: center;
  list-style-type: none;
}
.Home .top-bar ul li {
  display: flex;
}
.Home .top-bar ul li button {
  display: inline-block;
  padding: 1rem;
  font-size: 2rem;
  transition-property: background-color color font-weight;
  transition-duration: 300ms;
  margin: 0 1rem;
  border-radius: 5px;
  color: white;
  letter-spacing: 0.1rem;
  font-weight: 300;
  text-transform: uppercase;
  background-color: rgba(0, 0, 0, 0);
  border: none;
}
.Home .top-bar ul li button:hover {
  color: black;
  background-color: white;
  font-weight: 400;
}
.Home h1 {
  font-size: 3.5rem;
  padding: 1rem;
  font-weight: 300;
  color: #EC8B5E;
}
.Home .splash-container {
  overflow: hidden;
  height: 50vh;
}
.Home .splash-container h1 {
  display: inline-block;
  position: absolute;
  top: 15%;
  left: 30%;
  transform: translateX(-50%);
  z-index: 10;
  font-weight: 500;
  font-size: 13rem;
  color: #EC8B5E;
  -webkit-filter: drop-shadow(-1px 1px 3px black);
          filter: drop-shadow(-1px 1px 3px black);
}
.Home .splash-container img {
  max-width: 100%;
  transform: translateY(-30%);
  -webkit-filter: blur(0px) brightness(90%);
          filter: blur(0px) brightness(90%);
  z-index: -1;
}
.Home .home-picker {
  margin-top: 4rem;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  width: 60%;
  font-size: 2.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.Home .home-picker select {
  font-size: 2.5rem;
  width: 40%;
}
.Home .home-picker button {
  width: 20%;
  font-size: 2.5rem;
  border: 1px solid black;
  margin-top: 2rem;
  background-color: #EC8B5E;
  border-radius: 15px;
}
.Home .google-signin {
  font-size: 4rem;
  position: relative;
  left: 50%;
  font-size: 3rem;
  transform: translateX(-50%);
  background-color: white;
  border: 2px solid dodgerblue;
}
.Home .contact-container {
  background-color: #141A46;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  color: white;
  display: flex;
  justify-content: center;
  font-size: 1.5rem;
  text-align: center;
}

@media (max-width: 1000px) {
  .Home .home-picker {
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    width: 70%;
    font-size: 2.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .Home .home-picker select {
    font-size: 2.5rem;
    width: 100%;
  }
  .Home .home-picker button {
    width: 50%;
    font-size: 2.5rem;
    border: 1px solid black;
    margin-top: 2rem;
    background-color: #EC8B5E;
    border-radius: 15px;
  }
  .Home .splash-container {
    overflow: hidden;
    position: static;
    height: 20vh;
  }
  .Home .splash-container img {
    max-width: 100%;
    transform: translateY(-30%);
    -webkit-filter: blur(0px) brightness(90%);
            filter: blur(0px) brightness(90%);
    z-index: -1;
    height: auto;
  }
}
